.rui-pull-refresh {
  position: relative;
  height: 100%;
  overflow: auto;
}
.rui-pull-refresh-loader {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.rui-pull-refresh-nomore {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
